import { Injectable } from '@angular/core';
import { AngularFirestore, AngularFirestoreCollection } from 'angularfire2/firestore';
import { functionality, message } from 'src/Table/table';
import { Observable } from 'rxjs';
import { map } from 'rxjs/internal/operators/map';
@Injectable({
  providedIn: 'root'
})
export class MessageService {
  private messageCollectionList: AngularFirestoreCollection<any>;
  private messageList: Observable<message[]>;
  constructor(private db: AngularFirestore) { 
    this.messageCollectionList = db.collection<message>('Message');
   }
   create(message) { 
    return this.messageCollectionList.add(message);
  }
  getAllMessage() {
    this.messageList = this.messageCollectionList.snapshotChanges().pipe(
      map(actions => {
        return actions.map(a => {
          const data = a.payload.doc.data();
          const id = a.payload.doc.id;
          return { id, ...data };
        });
      })
    );
    return this.messageList;
  }
updateMessage(funObj:functionality,id:string){
  return this.messageCollectionList.doc(id).update(funObj);
}
removeMessage(id:string){
  return this.messageCollectionList.doc(id).delete()
}
}
